.hero {
    align-items: center;
    background-image: url("../../imagens/fundo.jpg");
    background-position: center;
    height: stretch; 
    min-height: 800px;
}

.divLateral {
  position: relative;	
  text-shadow:
  -1px -1px 0 #000,
  1px -1px 0 #000,
  -1px 1px 0 #000,
  1px 1px 0 #000;
  color: azure;
}

.divMeio {
  margin-top: 35px;
  width: 100%;
  position: relative;	
  font-size: 45px;
  text-align: center;
  text-shadow:
  -1px -1px 0 #000,
  1px -1px 0 #000,
  -1px 1px 0 #000,
  1px 1px 0 #000;
  color: azure;
}

.divTitulo {
  text-shadow:
		-1px -1px 0 #000,
		1px -1px 0 #000,
		-1px 1px 0 #000,
		1px 1px 0 #000;
  color: azure;
  font-size: 8vw;
  text-align: center;
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  font-weight: bold;
  width: 70vw;
}

@keyframes marquee {
    0% {
      background-position: 0px;
    }
  
    100% {
      background-position: -1900px;
    }
  }

