.card .card-content {
    padding: unset;
}

.card .card-image {
    padding: unset;
}

.info{
    padding: 5px;
    text-align: center;
}

.row{
    width: 100%;
}

.divTitulo {
    text-shadow:
          -1px -1px 0 #000,
          1px -1px 0 #000,
          -1px 1px 0 #000,
          1px 1px 0 #000;
    color: azure;
    font-size: 5vw;
    text-align: center;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    font-weight: bold;
    width: 70vw;
  }

.waves-effect.waves-brown .waves-ripple button{
    /* The alpha value allows the text and background color
    of the button to still show through. */
     background-color: rgba(121, 85, 72, 0.65);
     color: #000;
}

.button{
    background-color:'red';
    color:'white';
    text-decoration: rgb(34, 204, 34);
}

.card-img-top {
    width: 100%;
    height: 20vw;
    object-fit: cover;
}

.myButton {
	box-shadow: 2px 2px 0px 0px #cc2f44;
	background:linear-gradient(to bottom, #1f2830 5%, #291717 100%);
	background-color:#1f2830;
	border-radius:10px;
	border:1px solid #00050a;
	display:inline-block;
	cursor:pointer;
	color:#ffffff;
	font-family:Arial;
	font-size:15px;
	font-weight:bold;
    padding:2px 10px;
    text-decoration: none;
    text-shadow:0px 1px 0px #c74848;
}
.myButton:hover {
	background:linear-gradient(to bottom, #291717 5%, #1f2830 100%);
    background-color:#291717;
    text-decoration: none;
}
.myButton:active {
	position:relative;
    top:1px;
    text-decoration: none;
}
